.projects{
    &__header{
        font-size: 50px;
        //font-style: italic;
        padding-bottom: 10px;
        font-family: "Times New Roman", Times, serif;


    }
}

.project{
    &__header{
        display: grid;
        grid-template-columns: 1fr 3fr;
        gap: 1rem;
        font-family: "Times New Roman", Times, serif;
        span{
            border-top: 1px solid #000;
            padding: 1rem;
            font-size: 26px;
            text-align: center;
        }

    }
    &__infos{
        margin-top: 1rem;
        &--name{
            position: relative;
            font-size: 2vw;
            font-family: "Lucida Console", "Courier New", monospace;
            display: flex;
            align-items: center;
            font-weight: 400;

        }
    }
    &__img{
        position: relative;
        width: 100vw;
        height: 700px;
        img{
            width: 60vw;
            height: 600px;
            object-fit: cover;
            position: absolute;
            right: 10vw;
            top: 3rem;

        }
    }
    &__links{
        padding-top: 3rem;
        button{
            &:before{
                background: #000;

            }
            &:after{
                display: none;
                //border-color: #000;
            }
        }
    }
    &__tags{
        position: absolute;
        bottom: 1rem;
        display: flex;
        flex-direction: column;
        gap:  1rem;

    }

}

@media (max-width: 1350px){
    .projects{
        padding: 3rem;
    }

    .project__img img{
        width: 74vw;
        right: 5rem;
    }
    .project__infos--name{
        font-size: 38px;
    }

    .project .coolCircleEyeButton{
        transform: translate(-14rem, -3rem);
    }
}

@media (max-width: 740px){
    .projects{

        padding: 1rem;
    }
    .project__tags{
        flex-direction: row;
        top: 0;
        left: 0;
        
    }
    .project__infos--name{
        font-size: 35px;
        span{
            display: none;
        }
    }
    .project__img img {
        width: 85vw;
        right: 3rem;
    }
}
@media (max-width: 440px){
    .project .coolCircleEyeButton{
        transform: translate(0);
    }
    .project__tags{

        gap: 6px;
        font-size: 14px;
    }
    .project__header span{
        font-size: 25px;
    }
}